.header{
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    background-color: rgb(230,230,230);
    z-index: 2;
}
nav{
    height: 3.5rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.nav__logo{
    font-size: 2rem;
    text-transform: uppercase;
    font-weight: bold;
    color:darkblue;
}
@media screen and (max-width:500px) {
    .nav__menu{
        position: absolute;
        top: 0;
        right: 0;
        background-color: red;
    }
    .nav__list{
        flex-direction: column;
    }
}
.nav__menu{

}
.nav__list{
    display: flex;
    column-gap: 2rem;
}
.nav__link{
    font-size: 1rem;
    font-weight: 500;
    font-family: inherit;
}
.nav__link:hover{
    color: red;
}

